import { NextRequest, NextResponse } from 'next/server';
import { createHmac, timingSafeEqual } from 'crypto';
import { getUser } from '@/lib/firebase/firestore';

/**
 * Erro customizado para falhas de autentica칞칚o/autoriza칞칚o em APIs
 */
export class ApiError extends Error {
  constructor(public status: number, message: string) {
    super(message);
    this.name = 'ApiError';
  }
}

/**
 * Valida a assinatura de um webhook usando Graceful Rotation (Chave Prim치ria e Secund치ria).
 * Implementa timingSafeEqual para prevenir ataques de temporiza칞칚o.
 * 
 * @param rawBody - O corpo bruto da requisi칞칚o.
 * @param signature - A assinatura recebida no header (ex: x-hub-signature-256).
 * @param primarySecret - A chave secreta atual.
 * @param secondarySecret - A chave secreta anterior (opcional).
 * @returns True se a assinatura for v치lida para qualquer uma das chaves.
 */
export function validateWebhookSignature(
  rawBody: string,
  signature: string,
  primarySecret: string,
  secondarySecret?: string
): boolean {
  const validate = (secret: string) => {
    const hmac = createHmac('sha256', secret);
    const digest = 'sha256=' + hmac.update(rawBody).digest('hex');
    
    // Preven칞칚o de Timing Attacks
    try {
      return timingSafeEqual(Buffer.from(signature), Buffer.from(digest));
    } catch {
      return false;
    }
  };

  try {
    // Tenta a chave prim치ria
    if (validate(primarySecret)) return true;

    // Tenta a chave secund치ria se existir (Graceful Rotation)
    if (secondarySecret && validate(secondarySecret)) {
      console.log('[Security] Webhook validado via chave secund치ria (Graceful Rotation)');
      return true;
    }

    return false;
  } catch (error) {
    console.error('[Security] Erro na valida칞칚o de assinatura:', error);
    return false;
  }
}

/**
 * Verifica se o usu치rio tem a role 'admin'
 * Baseado no ID Token passado no header Authorization
 */
export async function verifyAdminRole(request: NextRequest) {
  const authHeader = request.headers.get('Authorization');
  
  if (!authHeader || !authHeader.startsWith('Bearer ')) {
    throw new ApiError(401, 'Autentica칞칚o necess치ria (Bearer token ausente)');
  }

  const idToken = authHeader.split('Bearer ')[1];
  
  try {
    // Como n칚o podemos usar firebase-admin no ambiente Windows 11 24H2 devido a restri칞칫es de sistema,
    // usamos a REST API do Firebase Auth para validar o token de forma segura no lado do servidor.
    const apiKey = (process.env.NEXT_PUBLIC_FIREBASE_API_KEY ?? '').trim();
    
    if (!apiKey) {
      throw new ApiError(500, 'Configura칞칚o do Firebase ausente (API Key)');
    }

    const response = await fetch(
      `https://identitytoolkit.googleapis.com/v1/accounts:lookup?key=${apiKey}`,
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ idToken }),
      }
    );

    if (!response.ok) {
      const errorData = await response.json();
      console.error('Firebase Auth REST API Error:', errorData);
      throw new ApiError(401, 'Token de autentica칞칚o inv치lido ou expirado');
    }

    const data = await response.json();
    const uid = data.users?.[0]?.localId;

    if (!uid) {
      throw new ApiError(401, 'Usu치rio n칚o encontrado no provedor de auth');
    }

    // Busca a role no Firestore usando o UID validado
    const user = await getUser(uid);
    
    if (!user) {
      throw new ApiError(403, 'Perfil de usu치rio n칚o encontrado no banco de dados');
    }

    if (user.role !== 'admin') {
      throw new ApiError(403, 'Acesso negado: Requer privil칠gios de administrador');
    }

    return user;
  } catch (error) {
    if (error instanceof ApiError) throw error;
    
    console.error('Security Verification Error:', error);
    throw new ApiError(500, 'Erro interno na verifica칞칚o de seguran칞a');
  }
}

/**
 * Helper para padronizar a resposta de erro de seguran칞a nas rotas Next.js
 */
export function handleSecurityError(error: unknown) {
  if (error instanceof ApiError) {
    return NextResponse.json(
      { error: error.message },
      { status: error.status }
    );
  }
  
  console.error('Unhandled Security Error:', error);
  return NextResponse.json(
    { error: 'Erro de seguran칞a n칚o especificado' },
    { status: 500 }
  );
}
